import Cookie from 'js-cookie'
export default {
    state: {
        token: ''
    },
    mutations: {
        // 设置token 增
        setToken(state, val){
            state.token = val
            Cookie.set('token', val)
        },
        // 清除token 删
        clearToken(state){
            state.token = ''
            Cookie.remove('token')
        },
        // 获取token 查
        getToken(state){
            // 如果当前Cookie中有token直接获取，如果没有从state中获取
            state.token = state.token || Cookie.get('token')
        }
    }
}